<template>
    <div class='root'>
        <action-bar title='订单通知'></action-bar>
        <div class='container'>
            <scroll-view class='content' :data="list" :noMoreData="noMoreData">
                <div class="divider"></div>
                <div v-for="item in list" :key="item.id">
                    <div class="news-item">
                        <div>订单编号：{{ item.orderAlias }}</div>
                        <div class="time">{{ item.createDateTime }}</div>
                        <div class="desc">{{ item.content }}</div>
                    </div>
                </div>
            </scroll-view>
        </div>
    </div>
</template>
<script>
import { noticeList } from '@/http/api'
export default {
  name: 'newsTypeList',
  components: {

  },
  data () {
    return {
      type: '', // 类型
      list: [], // 消息list
      start: 0,
      limit: 10,
      noMoreData: false, // 是否有更多数据
      isRead: false
    }
  },
  methods: {
    // 获取消息数据
    getNoticeList () {
      noticeList({
        type: this.type,
        start: this.start,
        limit: this.limit
      }).then(res => {
        const data = res.data
        let list = this.list
        if (data == null) {
          list = data
        } else {
          list.push(...data)
        }
        let noMoreData = false
        if (data.length < 10) {
          noMoreData = true
        }
        this.list = list
        this.noMoreData = noMoreData
        this.isRead = true
      })
    }
  },
  created () {
    this.type = this.$route.query.type
  },
  mounted () {
    this.getNoticeList()
  }
}
</script>
<style lang="scss" scoped>
.root {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.container {
    flex-grow: 1;
    position: relative;
    background-color: #F2F2F2;
}

.content {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.divider{
  height: .375rem;
}

.news-item{
  border-radius: .25rem;
  background-color: white;
  margin-left: .375rem;
  margin-bottom: .375rem;
  margin-right: .375rem;
  padding: .375rem;
  font-size: .875rem;
}

.news-item .time{
  color: #b4b4b4;
}

.news-item .desc{
  color: #999999;
  margin-top: 12rpx;
}
</style>
